import React, { Component } from 'react';
import '../App.css';
import { connect } from "react-redux"
import { Button, Modal, Spin, Progress, Table, Input, Divider } from 'antd';
import { CopyOutlined } from '@ant-design/icons';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import Node from '../ABI/node';
import { Toast } from 'antd-mobile'
const { TextArea } = Input;

class node extends Component {
  constructor(props) {
    super(props);
    this.state = {
      version: '45',
      classSelect: "",
      Nodisplay: 'close',
      nodeWindow: false,
      downloadAdrWindow: false,
      reportNodeWindow: false,
      PushUpdateInfoWindow: false,
      PushinfoWindow: false,
      nodeList: {},
      nodeInfoWindow: '',
      nodeInfo: '',
      newVer: '',
      url: '',
      loadwindow: false, //
      loading: 'block', //
      complete: 'none', //
      loadmessage: '',  //
      nodecreater: '',
      nodeUrl: '',
      nodeVer: '',
      downloadAdr: '',
      reportNodeMsg: 'none',
      notNewVerMsg: 'none',
      UpdateInfo: '',
      info: '',
      urllist: ''
    }
  }

  async componentDidMount() {
    const _url = window.location.host;
    this.setState({ url: 'https://' + _url })

    const newVer = await Node.methods.newVer().call();
    this.setState({ newVer });

    const UpdateInfo = await Node.methods.UpdateInfo().call();
    if (UpdateInfo !== '' && newVer !== this.state.version) {
      this.setState({ UpdateInfo })
      this.setState({ PushUpdateInfoWindow: true });
    }

    const info = await Node.methods.info().call();
    if (info !== '') {
      this.setState({ info })
      this.setState({ PushinfoWindow: true });
    }

  }

  reportNode = async () => {
    await Node.methods.reportNode(
      this.state.url,
      this.state.version
    )
      .send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.setState({
            loadmessage: "",
            loadwindow: true,
            loading: 'block',
            complete: 'none',
            reportNodeWindow: false
          })
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.setState({
            loadmessage: "",
            loading: 'none',
            complete: 'block',
            loadwindow: false
          })
        }
      })
  }

  nodeInfoOpen = async () => {
    if (this.props.ChainID !== '0x80') {
      return Toast.show({ content: 'HECO' })
    } else {
      this.setState({
        loadmessage: "",
        loadwindow: true,
        loading: 'block',
        complete: 'none'
      })
      const AllNodes = await Node.methods.AllNodes().call();
      const dataSource = [];
      var urllist = '()\n';
      for (var i = 0; i < AllNodes.length; i++) {
        const node = await Node.methods.getNode(AllNodes[i]).call();
        dataSource.push(node);
        urllist = urllist + i + '' + node[1] + '\n';
      }
      this.setState({ nodeList: dataSource })
      this.setState({ nodeWindow: true })
      this.setState({ urllist })
      this.setState({
        loadmessage: "",
        loadwindow: false,
        loading: 'none',
        complete: 'block'
      })
    }
  }

  downloadAdr = async () => {
    if (this.props.ChainID !== '0x80') {
      return Toast.show({ content: 'HECO' })
    } else {
      const downloadAdr = await Node.methods.downloadAdr().call();
      console.log(downloadAdr)
      this.setState({ downloadAdr })
      this.setState({ downloadAdrWindow: true })
    }
  }

  nodeVersion = async () => {
    if (this.props.ChainID !== '0x80') {
      return Toast.show({ content: 'HECO' })
    } else {
      const getNodeVer = await Node.methods.getNodeVer(this.state.url).call();
      const newVer = await Node.methods.newVer().call();
      if (getNodeVer !== this.state.version) {
        this.setState({ reportNodeWindow: true });
        this.setState({ reportNodeMsg: 'block' });
      }
      if (newVer !== this.state.version) {
        this.setState({ reportNodeWindow: true });
        this.setState({ notNewVerMsg: 'block' });
      }
    }
  }

  UpdateInfo = async () => {
    const UpdateInfo = await Node.methods.UpdateInfo().call();
    this.setState({ UpdateInfo })
  }

  //
  loadwindowClose = () => {
    this.setState({ loadwindow: false })
  };

  //
  nodeWindowClose = () => {
    this.setState({ nodeWindow: false })
  };
  //
  nodeInfoClose = () => {
    this.setState({ nodeInfoWindow: false })
  };
  //
  downloadAdrClose = () => {
    this.setState({ downloadAdrWindow: false })
  };
  //
  reportNodeWindowClose = () => {
    this.setState({ reportNodeWindow: false })
  };
  //
  PushUpdateInfoWindowClose = () => {
    this.setState({ PushUpdateInfoWindow: false })
  };
  //
  PushinfoWindowClose = () => {
    this.setState({ PushinfoWindow: false })
  };

  chackChainId = async (chainName, to, msg) => {
    if (this.state.ChainName !== chainName) {
      return Toast.show({ content: msg })
    } else {
      this.props.history.push(to)
    }
  }

  render() {

    const columns = [
      {
        title: '',
        dataIndex: '1',
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: 'Ver',
        dataIndex: '2',
        width: 50,
        align: 'center',
      }
    ];

    return (

      <div >
        <div>
          <Button size="small"
            onClick={this.nodeVersion}
          >V{this.state.version}
          </Button>
          <Button size="small"
            onClick={this.nodeInfoOpen}
          >
          </Button>
          <Button size="small"
            onClick={this.downloadAdr}
          >V{this.state.newVer}
          </Button>
        </div>
        {/*  */}
        <Modal visible={this.state.reportNodeWindow}
          onCancel={this.reportNodeWindowClose}
          width={280}
          footer={[]}
        >
          <div style={{ marginTop: '20px', width: '90%', display: this.state.reportNodeMsg }} >
            <p>*</p>
            <p>*</p>
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.reportNode}></Button>
            </div>
          </div>
          <Divider />
          <div style={{ marginTop: '10px', width: '90%', display: this.state.notNewVerMsg }}>
            <p>*</p>
            <p>*</p>
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.downloadAdr}></Button>
            </div>
          </div>
        </Modal>

        {/*   */}
        <Modal visible={this.state.nodeWindow}
          onCancel={this.nodeWindowClose}
          footer={[
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.nodeWindowClose}></Button>
            </div>,
          ]}
        >
          <Table size='small' pagination={false} columns={columns}
            style={{ marginTop: '12px' }}
            dataSource={this.state.nodeList}
            customRow="click"
            onRow={(record) => {
              return {
                onClick: () => {
                  this.setState({ nodeInfoWindow: true })
                  this.setState({ nodecreater: record[0] })
                  this.setState({ nodeUrl: record[1] })
                  this.setState({ nodeVer: record[2] })
                }
              }
            }
            }
          />
          <Modal title='' visible={this.state.nodeInfoWindow}
            onCancel={this.nodeInfoClose}
            footer={[
              <div style={{ textAlign: 'center' }} >
                <Button type='primary' onClick={this.nodeInfoClose}>
                  
                </Button>
              </div>,
            ]}
          >
            <div>
              <Input id='i1' addonBefore="" value={this.state.nodecreater}
                disabled
              />
              <Input id='i1' addonBefore="" value={this.state.nodeUrl}
                disabled style={{ marginTop: '5px' }}
                suffix={
                  <CopyToClipboard text={this.state.nodeUrl}>
                    <Button size="small" ><CopyOutlined /></Button>
                  </CopyToClipboard>
                }
              />
              <Input id='i1' addonBefore="" value={this.state.nodeVer}
                disabled style={{ marginTop: '5px' }}
              />
            </div>

          </Modal>
          <CopyToClipboard text={this.state.urllist}>
            <Button block type='primary'></Button>
          </CopyToClipboard>
        </Modal>
        {/*  */}
        <Modal visible={this.state.PushUpdateInfoWindow}
          onCancel={this.PushUpdateInfoWindowClose}
          width={280}
          footer={[
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.downloadAdr}>
                
              </Button>
            </div>
          ]}
        >
          <div style={{ marginTop: '15px' }}>
            <TextArea id='i1' rows={6} value={this.state.UpdateInfo} disabled />
          </div>
        </Modal>
        {/*  */}
        <Modal visible={this.state.PushinfoWindow}
          onCancel={this.PushinfoWindowClose}
          width={280}
          footer={[
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.PushinfoWindowClose}>
                
              </Button>
            </div>
          ]}
        >
          <div style={{ marginTop: '15px' }}>
            <TextArea id='i1' rows={6} value={this.state.info} disabled />
          </div>
        </Modal>
        {/*  */}
        <Modal visible={this.state.downloadAdrWindow}
          onCancel={this.downloadAdrClose}
          footer={[
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.downloadAdrClose}>
                
              </Button>
            </div>
          ]}
        >
          <div style={{ marginTop: '12px', width: '90%' }}>
            <p>
              {this.state.downloadAdr}
              <CopyToClipboard text={this.state.downloadAdr}>
                <Button size="small"><CopyOutlined /></Button>
              </CopyToClipboard>
            </p>
            <p>.rar</p>
          </div>
        </Modal>

        {/*   */}
        <Modal visible={this.state.loadwindow}
          onCancel={this.loadwindowClose}
          width={250}
          footer={[
            <div style={{ textAlign: 'center' }} >
              <Button type='primary' onClick={this.loadwindowClose}></Button>
            </div>,
          ]}
        >
          <div style={{ textAlign: 'center' }}>
            <div style={{ display: this.state.loading }}>
              <Spin size="large" />
              <div style={{ color: "#1890ff", fontSize: "13px", textAlign: 'center' }}>
                {this.state.loadmessage}
              </div>
            </div>
            <div style={{ display: this.state.complete }}>
              <Progress type="circle" percent={100} width={45} />
              <div style={{ color: "green", fontSize: "13px", textAlign: 'center' }}>
                {this.state.loadmessage}
              </div>
            </div>
          </div>
        </Modal>

      </div>


    );
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(node)
